function system (archetypes, powers) {
  const sys = {
    powers,
    archetypes: {},
    emptySelections: () => ({
      name: "",
      blurb: "",
      level: 1,
      archetypes: [],
      stats: {
        Evasion: 0,
        Defense: 0,
        Resilience: 0,
        Speed: 0,
        Energy: 0,
        Focus: 0
      },
      affinities: [],
      powers: [],
    })
  }

  sys.apply = (selections) => {
    const character = {
      name: selections.name,
      blurb: selections.blurb,
      level: selections.level,
      archetypes: selections.archetypes.map(arch => sys.archetypes[arch]),
      stats: {
        Evasion: selections.stats.Evasion,
        Defense: selections.stats.Defense,
        Resilience: selections.stats.Resilience,
        Speed: selections.stats.Speed,
        Energy: selections.stats.Energy,
        Focus: selections.stats.Focus
      },
      powers: [],
      selections
    }

    Object.values(character.archetypes).forEach(archetype => {
      console.log(archetype)
      Object.values(archetype.basePowers).forEach(power => {
        character.powers.push(power)
      })
      selections.upgrades.forEach(upgrade => {
        if (Object.keys(archetype.upgrades).includes(upgrade)) {
          character.powers.push(sys.powers[upgrade])
        }
      })
    })

    return character
  }

  Object.keys(archetypes).forEach(arch => {
    const basePowers = {}
    archetypes[arch].basePowers.forEach(pow => basePowers[pow] = sys.powers[pow])
    const upgrades = {}
    archetypes[arch].upgrades.forEach(pow => upgrades[pow] = sys.powers[pow])
    sys.archetypes[arch] = {
      name: archetypes[arch].name,
      basePowers,
      upgrades,
    }
  }, {})

  return sys
}

export default system
