<script>
import {showing} from "./controls.js"
import {system, prototypes, characters, monsters, rules} from "./dataStreams.js"

let choices = {
  rules: {
    name: "Rules",
    children: {}
  },
  archetypes: {
    name: "Archetypes",
    children: {},
    open: false
  },
  prototypes: {
    name: "Prototypes",
    children: {},
    open: false
  },
  characters: {
    name: "Characters (cached)",
    children: {},
    open: false
  },
  monsters: {
    name: "Monsters",
    children: {},
    open: false
  }
}

function setup_rules(rules) {
  choices.rules.children = {
    combat: {name: "Combat", details: rules.combat}
  }
}

$: if ($rules) {
  setup_rules($rules)
}

function setup_system(sys) {
  choices.archetypes.children = {}

  Object.keys(sys.archetypes).forEach(cls => {
    choices.archetypes.children[cls] = sys.archetypes[cls]
  })
}

$: if ($system) {
  setup_system($system)
}

function setup_prototypes(protos) {
  choices.prototypes.children = {}
  Object.keys(protos).forEach(prot => {
    choices.prototypes.children[prot] = protos[prot]
  })
}

$: if ($prototypes) {
  setup_prototypes($prototypes)
}

function setup_monsters(monsters) {
  choices.monsters.children = {}
  Object.keys(monsters).forEach(m => {
    choices.monsters.children[m] = monsters[m]
  })
}

$: if ($monsters) {
  setup_monsters($monsters)
}

function setup_characters(chars) {
  choices.characters.children = {}
  Object.keys(chars).forEach(char => {
    chars[char].id = char
    choices.characters.children[char] = chars[char]
  })
}

$: if ($characters) {
  setup_characters($characters)
}

let selection = {
  heading: "",
  option: ""
}
showing.subscribe(([heading, option]) => {
  selection = {
    heading,
    option
  }
})
</script>


<aside>
  {#each Object.keys(choices) as heading}
  <p on:click={() => choices[heading].open = !choices[heading].open} class="openable"><b>{choices[heading].name}<b></p>
    {#if choices[heading].open}
      {#each Object.keys(choices[heading].children) as option}
      	<a href="#top" on:click={() => showing.next([heading, choices[heading].children[option]])}
           class="navigation {selection.heading === heading && selection.option.name === choices[heading].children[option].name ? 'active' : ''}">
      		<span class="navigation">{choices[heading].children[option].name}</span>
      	</a>
      {/each}
    {/if}
  {/each}
</aside>
