import { combineLatest, timer, BehaviorSubject, Subject } from "rxjs"
import { map, tap, switchMap } from "rxjs/operators"
import { fromFetch } from 'rxjs/fetch'
import yaml from "js-yaml"

import system from "./system.js"


const sys = new BehaviorSubject(null)
const prototypes = new BehaviorSubject([])
const monsters = new BehaviorSubject([])
const characters = new BehaviorSubject(JSON.parse(localStorage.getItem("characters") || "{}"))
const save = new Subject()
const rules = new BehaviorSubject("")

function ingestYaml(path) {
  return fromFetch(path).pipe(
    switchMap(response => response.text()),
    map(text => yaml.load(text))
  )
}


function ingestHTML(path) {
  return fromFetch(path).pipe(
    switchMap(response => response.text())
  )
}


combineLatest([
  ingestHTML("./data/combat.html")
]).pipe(
  map(([combat]) => { return {combat} })
).subscribe(rules)


combineLatest([
  ingestYaml("./data/archetypes.yaml"),
  ingestYaml("./data/powers.yaml"),
]).pipe(
  map(([archetypes,
        powers]) => system(archetypes, powers))
).subscribe(sys)


ingestYaml("./data/prototypes.yaml").subscribe(prototypes)
ingestYaml("./data/monsters.yaml").subscribe(monsters)

save.subscribe(([id, character]) => {
  const chars = characters.getValue()
  chars[id] = character
  localStorage.setItem("characters", JSON.stringify(chars))
  characters.next(chars)
})

function saveCharacter (id, character) {
  save.next([id, character])
}

export {
  rules,
  sys as system,
  prototypes,
  monsters,
  characters,
  saveCharacter as save
}
